import React, {
  useEffect,
  useState,
  useRef,
  useCallback,
  useMemo,
} from "react";
import AutoSizer from "react-virtualized-auto-sizer";
import { VariableSizeGrid as Grid } from "react-window";
import { debounce } from "utils/lodash";

import { useFetch, useLocalStorage, useResizeObserver } from "hooks";
import { CircularProgress } from "components";
import { createTimeouts } from "utils";

import SlotGameElement from "../SlotGameElement";
import { CategoryFetch, SlotGame } from "../../../../types";
import { windowSizeWhenStartScaleSlotElements } from "../Slider";
import css from "./style.module.scss";

interface Props {
  sort: string;
  providerFilter: string;
  fetchEndpoint: string;
}

interface PaginationParams {
  current_page: number;
  total_pages: number;
}

const initPagParams: PaginationParams = {
  current_page: 1,
  total_pages: 1,
};

const SlotsGrid = ({ sort, providerFilter, fetchEndpoint }: Props) => {
  const outerRef = useRef<HTMLDivElement>(null);
  const [rootElement, setRootElement] = useState<HTMLDivElement | null>(null);
  const [token] = useLocalStorage("token");

  const [fetch, { data: gamesData }] = useFetch<CategoryFetch>(
    `slots/${fetchEndpoint}`,
  );
  const [games, setGames] = useState<SlotGame[]>([]);
  const [showLoader, setShowLoader] = useState<boolean>(true);
  const [isRender, setIsRender] = useState(true);
  const [pagParams, setPagParams] = useState<PaginationParams>(initPagParams);

  useEffect(() => {
    setShowLoader(true);
    fetch({
      variables: {
        page: 1,
        popularity_filter: sort,
        provider_filter: providerFilter === "ALL" ? "" : providerFilter,
      },
    });
  }, [sort, providerFilter, token]);

  useEffect(() => {
    if (gamesData) {
      if (gamesData.current_page === 1) {
        setGames(gamesData.games);
        outerRef.current?.scrollTo({ top: 0 });
      } else {
        setGames(games.concat(gamesData.games));
      }
      setPagParams({
        current_page: gamesData.current_page,
        total_pages: gamesData.total_pages,
      });
      setShowLoader(false);
    }
  }, [gamesData]);

  const { clearTimeouts, pushTimeout } = useMemo(() => createTimeouts(), []);

  useResizeObserver(
    () => {
      clearTimeouts();
      setIsRender(true);

      pushTimeout(() => setIsRender(false), 0);
    },
    rootElement,
    [rootElement],
  );

  const loadMoreGames = useCallback(
    debounce(
      (current: number) => {
        fetch({
          variables: {
            page: current,
            popularity_filter: sort,
            provider_filter: providerFilter === "ALL" ? "" : providerFilter,
          },
        });
      },
      500,
      {
        leading: true,
        trailing: false,
      },
    ),
    [providerFilter, sort],
  );

  const cellSize = 180;

  const onScroll = (event: any) => {
    if (!outerRef.current) {
      return;
    }
    if (
      outerRef.current.scrollHeight -
        event.scrollTop -
        outerRef.current.offsetHeight <
      5
    ) {
      if (pagParams.current_page < pagParams.total_pages) {
        loadMoreGames(pagParams.current_page + 1);
      }
    }
  };

  return (
    <div className={css.root} ref={setRootElement}>
      {!isRender && !showLoader ? (
        <AutoSizer>
          {({ height, width }) => {
            if (width < windowSizeWhenStartScaleSlotElements) {
              width = windowSizeWhenStartScaleSlotElements;
            }
            const colsAmount = Math.floor(width / cellSize);
            const imgSize = Math.floor((width - 7) / colsAmount);

            return (
              <Grid
                outerRef={outerRef}
                className={css.grid}
                height={height}
                width={width}
                overscanCount={10}
                columnCount={colsAmount}
                onScroll={onScroll}
                columnWidth={() => imgSize}
                rowCount={Math.ceil(games.length / colsAmount)}
                rowHeight={() => imgSize}
                itemData={games}>
                {({ data, rowIndex, columnIndex, style }) => {
                  const index = rowIndex * colsAmount + columnIndex;
                  if (index >= data.length) {
                    return null;
                  }
                  return (
                    <SlotGameElement style={style} element={data[index]} />
                  );
                }}
              </Grid>
            );
          }}
        </AutoSizer>
      ) : (
        <div className={css.loaderWrapper}>
          <CircularProgress className={css.loader} size={100} color="inherit" />
        </div>
      )}
    </div>
  );
};

export default React.memo(SlotsGrid);
