import React, { CSSProperties, useMemo } from "react";
import SwipeableViews from "react-swipeable-views";

import { useChat, useWindowSize } from "providers";

import SlotsGrid from "../SlotsGrid";
import css from "./style.module.scss";

interface Props {
  selectedIndex: number;
  provider: string;
  sort: string;
}

export const windowSizeWhenStartScaleSlotElements = 420;

const Slider = ({ selectedIndex, provider, sort }: Props) => {
  const { width } = useWindowSize();
  const { isHide } = useChat();

  const style: CSSProperties = useMemo(() => {
    if (width < windowSizeWhenStartScaleSlotElements) {
      const scale = (width / windowSizeWhenStartScaleSlotElements).toFixed(3);
      const sizeDifference = (windowSizeWhenStartScaleSlotElements - width) / 2;

      return {
        transform: `scale(${scale})`,
        right: sizeDifference,
        bottom: sizeDifference * 2,
        marginBottom: sizeDifference * 2 * -1,
      };
    } else {
      return {};
    }
  }, [width, isHide]);

  return (
    <div className={css.root} style={style}>
      <SwipeableViews index={selectedIndex} disabled>
        <SlotsGrid
          providerFilter={provider}
          sort={sort}
          fetchEndpoint="get-all-games"
        />
        <SlotsGrid
          providerFilter={provider}
          sort={sort}
          fetchEndpoint="get-hot-games"
        />
        <SlotsGrid
          providerFilter={provider}
          sort={sort}
          fetchEndpoint="get-live-games"
        />
        <SlotsGrid
          providerFilter={provider}
          sort={sort}
          fetchEndpoint="get-recently-played"
        />
        <SlotsGrid
          providerFilter={provider}
          sort={sort}
          fetchEndpoint="get-favourite-games"
        />
      </SwipeableViews>
    </div>
  );
};

export default React.memo(Slider);
