import { GameInfo } from "pages/SlotGames/types";
import { track } from "utils";

export const defaultGameTitle = "Pasino Slot Game";

const findId = (search: string): string => {
  const searchSplitLine = search.split(/\?|=/);
  return searchSplitLine.find((value) => !!value) || "";
};

export const getGameInfoFromSearch = (
  search: string,
  oldId: string,
): GameInfo | undefined => {
  try {
    const newId = findId(search).replace("%3A", ":");
    if (newId !== oldId) {
      const [provider, title] = newId.split(":");

      return {
        id: newId,
        title,
        provider,
      };
    }
  } catch (error) {
    track()?.errors(error);

    console.error("error when try get GameInfoFromSearch", error);
  }
};
