import React, { FC, useEffect, useState } from "react";
import { useHistory } from "react-router";
import { fetchBase } from "utils";
import { ListState, ProvidersType } from "../types";

import Context from "./Context";

const categories = [
  "All games",
  "Hot games",
  "Live games",
  "Recently Played",
  "Favorite games",
];
const searchCategories = ["all", "hot", "live", "recently", "favorite"];

const Provider: FC = ({ children }) => {
  const history = useHistory();

  const [selectedCategory, setSelectedCategory] = useState<number>(0);
  const [providersState, setProviders] = useState<ListState>({
    all: ["All"],
    selected: "All",
  });
  const [sortState, setSortState] = useState<ListState>({
    all: ["Most Popular", "Least Popular"],
    selected: "Most Popular",
  });

  const changeSelectedProvider = (value: string | number) => {
    const list = { ...providersState };
    list.selected = value as string;
    setProviders(list);
  };

  const changeSelectedSort = (value: string | number) => {
    const list = { ...sortState };
    list.selected = value as string;
    setSortState(list);
  };

  useEffect(() => {
    const getProviders = async () => {
      const { data } = await fetchBase<ProvidersType>("slots/get-providers");
      if (data?.providers.length) {
        setProviders({
          ...providersState,
          all: ["All", ...data.providers],
        });
      }
    };

    getProviders();
  }, []);

  useEffect(() => {
    const { location } = history;
    const params = new URLSearchParams(location.search);
    const category = params.get("category");
    const provider = params.get("provider");

    const clearHistory = (param: string) => {
      params.delete(param);
      history.replace({ ...location, search: params.toString() });
    };

    if (category) {
      const index = searchCategories.findIndex(
        (val) => val.toLowerCase() === category.toLowerCase(),
      );
      if (index >= 0) {
        setSelectedCategory(index);
        clearHistory("category");
      }
    }

    if (provider && providersState.all.length > 1) {
      const activeProvider = providersState.all.find(
        (prv) => prv.toLowerCase() === provider.toLocaleLowerCase(),
      );
      changeSelectedProvider(activeProvider || providersState.all[0]);
      clearHistory("provider");
    }
  }, [history, providersState.all]);

  return (
    <Context.Provider
      value={{
        providers: {
          values: providersState,
          setSelected: changeSelectedProvider,
        },
        sort: {
          values: sortState,
          setSelected: changeSelectedSort,
        },
        categories: {
          all: categories,
          value: selectedCategory,
          setValue: (value) => setSelectedCategory(value),
        },
      }}>
      {children}
    </Context.Provider>
  );
};

export default Provider;
