import { fetchBase } from "utils";

export interface GamesListElement {
  id: string;
  image_url: string;
  name: string;
  provider: string;
}

interface GamesList {
  games: GamesListElement[];
}

export const getGamesList = async (
  setGames: (games: GamesListElement[]) => void,
) => {
  const { data } = await fetchBase<GamesList>("slots/get-featured-slots");

  if (data?.games) {
    setGames(data.games);
  }
};
