import React from "react";

import { useSlots } from "pages/SlotGames/Provider";
import { Selector } from "./Selector";
import SearchHeader from "../SearchHeader";
import css from "./style.module.scss";

export const Header = () => {
  const { providers, sort } = useSlots();
  return (
    <div className={css.root}>
      <SearchHeader />

      <Selector
        title="Providers"
        items={providers.values.all}
        selectedValue={providers.values.selected}
        setSelectedValue={providers.setSelected}
      />
      <Selector
        title="Sort by"
        items={sort.values.all}
        selectedValue={sort.values.selected}
        setSelectedValue={sort.setSelected}
      />
    </div>
  );
};
