import React, { CSSProperties } from "react";
import { useHistory } from "react-router";
import { useGetLocale } from "providers";

import { useLocalStorage, useModalLocation } from "hooks";
import notifybarManager from "components/NotifyBar/notifybar-manager";
import { ReactComponent as PlayArrowRounded } from "assets/icons/play-arrow-rounded.svg";

import { SlotGame } from "../../../../types";
import css from "./style.module.scss";

import { ParamKeys } from "config/routes";

interface Props {
  element: SlotGame;
  style: CSSProperties;
}

const SlotGameElement = ({ element, style }: Props) => {
  const intl = useGetLocale();

  const [token] = useLocalStorage("token");
  const history = useHistory();
  const {
    pushHistoryTo,
    location: { search },
  } = useModalLocation("register");

  const openModal = (value: ParamKeys) => {
    pushHistoryTo({ search: { name: "page", value } });
  };

  const changePage = (path: string, gameId: string) => {
    history.push({
      pathname: path + `/${gameId}`,
    });
  };

  const onSlotClick = (id: string) => {
    if (token) {
      changePage("slot", id);
    } else {
      notifybarManager.error({
        message: intl.formatMessage({
          id: "crashInterface.messages.noLogin",
        }),
        timeOut: 4000,
      });

      openModal("register");
    }
  };

  return (
    <div className={css.root} style={style}>
      <div className={css.container} onClick={() => onSlotClick(element.id)}>
        <img className={css.backgroundImg} src={element.image_url} />
        <div className={css.hover}>
          <span>{element.name}</span>
          <button className={css.button}>
            <PlayArrowRounded
              fontSize="large"
              style={{ color: "#FFFFFF", width: "35px", height: "35px" }}
            />
          </button>
          <span>{element.provider}</span>
        </div>
      </div>
    </div>
  );
};

export default React.memo(SlotGameElement);
