import React, { useRef } from "react";
import { VariableSizeList as List } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import clsx from "clsx";

import { ClickAwayListener, Popper } from "components";
import { SlotGame } from "pages/SlotGames/types";
import { useWhiteTheme } from "providers";
import { debounce } from "utils/lodash";

import SlotGameElement from "../../SlotsSlider/components/SlotGameElement";
import { PaginationParams } from "../../SearchHeader";
import css from "./style.module.scss";

const wait = debounce(
  (f: () => void) => {
    f();
  },
  500,
  {
    leading: true,
    trailing: false,
  },
);

interface Props {
  element: HTMLInputElement | null;
  open: boolean;
  onClose: () => void;
  pagParams: PaginationParams;
  data?: SlotGame[];
  loadPage: (val: number) => void;
}

const SearchResult = ({
  open,
  element,
  data,
  onClose,
  pagParams,
  loadPage,
}: Props) => {
  const { isWhiteThemeActive } = useWhiteTheme();
  const outerRef = useRef<HTMLDivElement>(null);

  const handleLoad = () => {
    wait(() => {
      loadPage(pagParams.current_page + 1);
    });
  };

  const onScroll = (event: any) => {
    if (!outerRef.current) {
      return;
    }
    if (
      outerRef.current.scrollWidth -
        event.scrollOffset -
        outerRef.current.offsetWidth <
      5
    ) {
      if (pagParams.current_page < pagParams.total_pages) {
        handleLoad();
      }
    }
  };

  const onOver = (e: any) => {
    if (outerRef.current) {
      outerRef.current.onwheel = (e) => {
        e.preventDefault();
        e.stopPropagation();
        outerRef.current?.scrollTo({
          top: 0,
          left: outerRef.current?.scrollLeft + e.deltaY,
        });
      };
    }
  };

  return (
    <ClickAwayListener onClickAway={onClose}>
      <Popper
        isOpen={open}
        position="bottom"
        anchor={element}
        className={clsx(css.root, {
          [css.root_white]: isWhiteThemeActive,
        })}
        classes={{
          childWrapperOptions: {
            open: css.searchOpen,
          },
        }}
        onMouseOver={onOver}
        margin={20}
        backgroundColor="black">
        {data?.length ? (
          <AutoSizer>
            {({ width, height }) => (
              <List
                outerRef={outerRef}
                className={css.list}
                height={height}
                width={width}
                itemCount={data.length}
                itemData={data}
                layout="horizontal"
                direction="horizontal"
                overscanCount={10}
                onScroll={onScroll}
                itemSize={() => 245}>
                {({ data, index, style }) => {
                  return (
                    <SlotGameElement element={data[index]} style={style} />
                  );
                }}
              </List>
            )}
          </AutoSizer>
        ) : null}
      </Popper>
    </ClickAwayListener>
  );
};

export default SearchResult;
