import React, { Dispatch, SetStateAction } from "react";
import { CategoriesState, ListState } from "../types";

interface ContextList {
  values: ListState;
  setSelected: (value: string | number) => void;
}

type SlotsContextType = {
  providers: ContextList;
  sort: ContextList;
  categories: CategoriesState;
};

const defaultValue: SlotsContextType = {
  providers: {
    setSelected: () => {},
    values: {
      all: [],
      selected: "",
    },
  },
  sort: {
    setSelected: () => {},
    values: {
      all: [],
      selected: "",
    },
  },
  categories: {
    all: [],
    value: 0,
    setValue: () => {},
  },
};

export const Context = React.createContext<SlotsContextType>(defaultValue);

export default Context;
