import React, {
  ChangeEvent,
  FC,
  useCallback,
  useEffect,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import { useFetch } from "hooks";
import { debounce } from "utils";
import { Icon, Input } from "components";
import { useWhiteTheme } from "providers";

import { CategoryFetch, SlotGame } from "pages/SlotGames/types";

import SearchResult from "./SearchResult";
import css from "./style.module.scss";

interface Props {}
export interface PaginationParams {
  current_page: number;
  total_pages: number;
}

const initPagParams: PaginationParams = {
  current_page: 1,
  total_pages: 1,
};

const wait = debounce();

const SearchHeader: FC<Props> = ({}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchResult, setSearchResult] = useState<SlotGame[]>([]);
  const [pagParams, setPagParams] = useState<PaginationParams>(initPagParams);
  const [searchKey, setSearchKey] = useState<string>("");

  const inputRef = useRef<HTMLInputElement>(null);

  const [searchFetch, { data }] = useFetch<CategoryFetch>("slots/search-games");

  const search = (value: string) => {
    if (value) {
      setSearchKey(value);
      searchFetch({
        variables: {
          query: value,
          page: 1,
          popularity_filter: "ASC",
          provider_filter: "",
        },
      });
    } else {
      setIsOpen(false);
    }
  };

  const delay = (
    event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
  ) => {
    wait(() => {
      const value = event.target.value;
      search(value);
    }, 500);
  };

  const handleSearchClose = () => {
    setIsOpen(false);
    if (inputRef.current) {
      inputRef.current.value = "";
    }
  };

  const handleSearchClick = () => {
    if (inputRef.current) {
      search(inputRef.current.value);
    }
  };

  const handleLoad = (page: number) => {
    searchFetch({
      variables: {
        page: page,
        query: searchKey,
        popularity_filter: "ASC",
        provider_filter: "",
      },
    });
  };

  useEffect(() => {
    if (data?.games) {
      setIsOpen(true);
      if (data.current_page === 1) {
        setSearchResult(data.games);
      } else {
        setSearchResult(searchResult.concat(data.games));
      }
      setPagParams({
        current_page: data.current_page,
        total_pages: data.total_pages,
      });
    }
  }, [data]);

  return (
    <div className={css.header}>
      <div className={css.inputWrapper}>
        <Input
          classes={{
            root: clsx(css.inputRoot),
            input: css.input,
          }}
          onChange={delay}
          placeholder={"Search Game"}
          ref={inputRef}
          startAdornment={
            <Icon variant="search" withoutButton className={css.searchIcon} />
          }
        />
        <SearchResult
          open={isOpen}
          element={inputRef.current}
          onClose={handleSearchClose}
          pagParams={pagParams}
          data={searchResult}
          loadPage={handleLoad}
        />
        <button className={css.buttonSearch} onClick={handleSearchClick}>
          SEARCH
        </button>
      </div>
    </div>
  );
};

export default SearchHeader;
