import React from "react";
import clsx from "clsx";

import { useWhiteTheme, useWindowSize } from "providers";

import { useSlots } from "pages/SlotGames/Provider";

import { SwitchTabs } from "./components/SwitchTabs";
import { Selector } from "../Header/Selector";
import Slider from "./components/Slider";
import css from "./style.module.scss";

export const SlotsSlider = () => {
  const { isWhiteThemeActive } = useWhiteTheme();
  const { categories, providers, sort } = useSlots();
  const { width } = useWindowSize();

  return (
    <div className={clsx(css.root, isWhiteThemeActive && css.whiteTheme)}>
      <div className={css.controlBox}>
        {width <= 800 ? (
          <Selector
            title={null}
            items={categories.all}
            selectedValue={categories.all[categories.value]}
            setSelectedValue={(value) => {
              const index = categories.all.findIndex(
                (category) => category === value,
              );
              categories.setValue(Math.max(index, 0));
            }}
            className={css.mobileBox}
          />
        ) : (
          <SwitchTabs
            categories={categories.all}
            value={categories.value}
            onChange={categories.setValue}
          />
        )}
      </div>

      <Slider
        selectedIndex={categories.value}
        provider={providers.values.selected}
        sort={sort.values.selected === sort.values.all[0] ? "DESC" : "ASC"}
      />
    </div>
  );
};
