import React, { useCallback } from "react";
import clsx from "clsx";

import { Tab, Tabs } from "components";

import css from "./style.module.scss";

interface Props {
  categories: string[];
  value: number;
  onChange: (val: number) => void;
}

export const SwitchTabs = ({ categories, value, onChange }: Props) => {
  const handleChange = useCallback((ev: any, val: number | string) => {
    onChange(val as number);
  }, []);

  return (
    <div className={css.root}>
      <Tabs
        value={value}
        onChange={handleChange}
        className={clsx(css.wrapper)}
        highlighterClass={clsx(css.indicator)}>
        {categories.map((value, index) => (
          <Tab
            key={value + index}
            label={value}
            classes={{ label: css.tabText, root: css.tab }}
          />
        ))}
      </Tabs>
    </div>
  );
};
