import React, { useEffect, useState } from "react";
import { Helmet } from "react-helmet";

import { GamesListElement, getGamesList } from "pages/SlotPage/utils";
import { GameList } from "pages/Home/components";

import { SlotsProvider } from "./Provider";
import { Header, SlotsSlider } from "./components";

import "config/themes/_slots.scss";
import css from "./style.module.scss";

export * from "./types";

const SlotGames = () => {
  const [gamesList, setGamesList] = useState<GamesListElement[]>([]);

  useEffect(() => {
    getGamesList(setGamesList);
  }, []);

  return (
    <>
      <Helmet>
        <title>Slots | Crypto Gambling Casino</title>
        <meta name="title" content="Play Bitcoin Slots - Pasino.com" />
        <meta
          name="description"
          content="We got all the Slot provider you love! Pragmatic, Evolution, Push Gaming, several live games and much more playable with Bitcoin.   "
        />
        <meta
          name="keywords"
          content="Play Slots Bitcoin, Evolution Bitcoin, Pragmatic Bitcoin, Netent Bitcoin, PushGaming Bitcoin,  "
        />
        <meta name="robots" content="index, follow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
      </Helmet>
      <div className={css.root}>
        <SlotsProvider>
          <Header />

          <GameList
            title="Featured Games"
            allLink={null}
            cardSize={165}
            rows={2}
            mobile={[
              {
                cardsAmount: 2,
                rowsAmount: 2,
                triggerSize: 600,
              },
            ]}
            data={
              gamesList?.map(({ id, image_url, name, provider }) => ({
                img: image_url,
                url: `slot/${id}`,
                name,
                about: `Provider: ${provider}`,
              })) || []
            }
          />
          <SlotsSlider />
        </SlotsProvider>
      </div>
    </>
  );
};

export default SlotGames;
