import React, { FC } from "react";
import clsx from "clsx";

import { BaseSelect, SelectItem } from "components";

import css from "./style.module.scss";

interface Props {
  title: string | null;
  selectedValue: string;
  setSelectedValue: (value: string | number) => void;
  items: string[];

  className?: string;
}

export const Selector: FC<Props> = ({
  title,
  selectedValue,
  setSelectedValue,
  items,

  className,
}) => {
  return (
    <BaseSelect
      component={
        <div className={css.selectBox}>
          {title && <h1 className={css.title}>{title}:</h1>}
          <span className={css.item}>{selectedValue}</span>
        </div>
      }
      classes={{
        root: clsx(css.select, className),
        label: css.selectLabel,
        expandIcon: css.selectExpandIcon,
      }}
      value={selectedValue}
      onChange={setSelectedValue}>
      {items.map((val) => (
        <SelectItem
          className={css.selectItem}
          key={`item_${val}`}
          value={val}
          label={val}
        />
      ))}
    </BaseSelect>
  );
};
